import React from 'react';
import { Link } from 'react-router-dom';
import { useState, useEffect } from 'react'
import supabase from '../api/Supabase';
import { Card, Space, Tag } from 'antd';
import { ArrowRightOutlined } from '@ant-design/icons';
import formatNumberCommas from '../utilities/NumberWithCommas';

const Home:React.FC = () => {
  const [latestProduct, setLatestProduct] = useState<any>([]);
  const [isLatestProduct, setIsLatestProduct] = useState(false);

  async function getLatestProduct() {
    let {data} = await supabase.from('product').select('*').order('id', { ascending: false }).limit(10);
    setLatestProduct(data);
    setIsLatestProduct(true);
  }

  useEffect(() => {
    getLatestProduct();
  }, []);

  return (
    <div>
      <div className="space-y-10">
        <div className="hero-image">
          <div className="hero-text w-full h-fit space-y-2">
            <div>
              <div className="brittany-title text-[4.5vw]">Welcome to</div>
              <div className="text-[8vw]">Jade Crochet.</div>
            </div>
          </div>
        </div>

        {
          (isLatestProduct && (
            <div className="w-full h-[380px]">
              <h2 className="brittany-title">THE LATEST</h2>
              <div className="h-[330px] overflow-x-scroll overflow-y-hidden">
                <div className="flex space-x-6">
                {
                  latestProduct.map((obj:any) => {
                    let {images} = obj;
                    return (
                      <Link key={obj.id}to={'/store/' + (obj.name.toLowerCase().replace(/\s/g, '-')) + '/' + obj.id}>
                        <Card
                          hoverable
                          className="w-[250px] h-[320px]"
                        >
                          <div className="flex justify-center h-[23vh] md:h-[32vh]">
                            <img className="h-full" src={images[0].url} />
                          </div>
                          <div className="mt-2 h-[2vh] md:h-[3vh]">
                            <span>{obj.name}</span>
                          </div>
                          <div className="mt-2">
                            {
                              obj.sale_percent ?
                              <div>
                                <div className="text-[#ff9933] mt-1">
                                  <Space>
                                    <del className="text-[12px] text-[#808080]">{formatNumberCommas(obj.price)}</del>
                                    <span className="font-bold">{formatNumberCommas(obj.sale_price)}</span>
                                  </Space>
                                </div>
                                <Tag color="#f50">-{obj.sale_percent.toFixed(0)}%</Tag>
                              </div>
                              :
                              <div className="text-[#ff9933] font-bold mt-1">{formatNumberCommas(obj.price)}</div>
                            }
                          </div>
                        </Card>
                      </Link>
                    )
                  })
                }
                </div>
              </div>
            </div>
          ))
        }

        <div className="text-center h-[40px]">
          <Link className="home-button" to="/store"><Space>VISIT MY STORE<ArrowRightOutlined /></Space></Link>
        </div>
      </div>
    </div>
  )
}

export default Home;
