import {createContext, useContext, ReactNode, useState} from 'react';
import ProductType from '../types/Product';

interface ShoppingCartProvider {
  children: ReactNode
}

interface CartItem extends ProductType {
  quantity: number;
}

interface ShoppingCartContext {
  increaseItemQuantity: (data:ProductType) => void;
  decreaseItemQuantity: (data:ProductType) => void;
  //removeItem: (id:number) => void;
  openCart: () => void;
  closeCart: () => void;
  isOpen: boolean;
  cartItems: CartItem[];
  cartQuantity: number;
}

const ShoppingCartContext = createContext({} as ShoppingCartContext);

export function useShoppingCart(){
  return useContext(ShoppingCartContext);
}

export function ShoppingCartProvider({ children }:ShoppingCartProvider){
  const [cartItems, setCartItems] = useState<CartItem[]>([]);
  const [isOpen, setIsOpen] = useState(false);
  const [cartQuantity, setCartQuantity] = useState(0);

  const openCart = () => {
    setIsOpen(true);
  }

  const closeCart = () => {
    setIsOpen(false);
  }

  const calcCardQuantity = (items:CartItem[]) => {
    const cartQuantity = items.reduce((quantity, item) => item.quantity + quantity, 0);
    setCartQuantity(cartQuantity);
  }

  const increaseItemQuantity = (data:ProductType) => {
    let currItems:CartItem[] = [];
    if(cartItems.find(item => item.id == data.id) == null){
      currItems = [...cartItems, {...data, quantity:1}];
    }
    else{
      cartItems.map((item:CartItem) => {
        if(item.id == data.id){
          item.quantity++;
        }
        else{
          return;
        }
      })
      currItems = cartItems;
    }
    setCartItems(currItems);
    calcCardQuantity(currItems);
  }

  const decreaseItemQuantity = (data:ProductType) => {
    let currItems:CartItem[] = [];
    if(cartItems.find(item => item.id == data.id)?.quantity == 1){
      currItems = currItems.filter(item => item.id != data.id);
    }
    else{
      cartItems.map((item:CartItem) => {
        if(item.id == data.id){
          item.quantity--;
        }
        else{
          return;
        }
      })
      currItems = cartItems;
    }
    setCartItems(currItems);
    calcCardQuantity(currItems);
  }

  return (
    <ShoppingCartContext.Provider value={{
      increaseItemQuantity,
      decreaseItemQuantity,
      openCart, closeCart, isOpen,
      cartItems, cartQuantity
    }}>
      {children}
    </ShoppingCartContext.Provider>
  )
}
