import { useState } from 'react';
import './App.css';
import { Routes,Route } from "react-router-dom";
import { Layout } from 'antd';
import {Navbar} from "./components/Navbar";
import {FooterBar} from "./components/Footer";
import {Cart} from "./components/Cart";
import Home from "./pages/Home";
import Chart from "./pages/Chart";
import Blog from "./pages/blog/BlogList";
import BlogDetail from "./pages/blog/BlogDetail";
import ProductList from "./pages/store/ProductList";
import ProductDetail from "./pages/store/ProductDetail";
import { useAuth0 } from "@auth0/auth0-react";
import ProductType from "./types/Product";
import { ShoppingCartProvider } from "./context/ShoppingCart";

const { Header, Content, Footer } = Layout;

function App() {
  const { user, isAuthenticated } = useAuth0();

  const [currentProductDetail, setCurrentProductDetail] = useState({});
  const viewProduct = (obj:ProductType) => {
    setCurrentProductDetail(obj);
  }

  return (
    <ShoppingCartProvider>
      <Layout className="min-h-[100vh]">
        <Cart />
        <Header className="h-fit p-0">
          <Navbar />
        </Header>
        <Content>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/blog" element={<Blog />} />
            <Route path="/store" element={<ProductList viewProduct={viewProduct}/>} />
            <Route path="/chart" element={<Chart />} />
            <Route path="/store/:title/:id" element={<ProductDetail detail={currentProductDetail}/>} />
            <Route path="/blog/:title/:id" element={<BlogDetail />} />
          </Routes>
        </Content>
        <Footer className="h-fit p-0">
          <FooterBar />
        </Footer>
      </Layout>
    </ShoppingCartProvider>
  );
}

export default App;
