import { useShoppingCart } from "../context/ShoppingCart";
import formatNumberCommas from '../utilities/NumberWithCommas';
import {Button, Space} from 'antd';
import { PlusOutlined, MinusOutlined, CloseOutlined } from '@ant-design/icons';

export function Cart() {
  const {isOpen, closeCart, cartItems, increaseItemQuantity, decreaseItemQuantity} = useShoppingCart();
  return (
    (isOpen) ? (
      <div className="relative z-10" aria-labelledby="slide-over-title" role="dialog" aria-modal="true">
        <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"></div>
        <div className="fixed inset-0 overflow-hidden">
          <div className="absolute inset-0 overflow-hidden">
            <div className="pointer-events-none fixed inset-y-0 right-0 flex max-w-full pl-10">
              <div className="pointer-events-auto w-screen max-w-md">
                <div className="flex h-full flex-col overflow-y-scroll bg-white shadow-xl">
                  <div className="flex-1 overflow-y-auto px-4 py-6 sm:px-6">
                    <div className="flex items-start justify-between">
                      <h2 className="text-lg font-medium text-gray-900" id="slide-over-title">Shopping cart</h2>
                      <div className="ml-3 flex h-7 items-center">
                        <button type="button" onClick={closeCart} className="relative -m-2 p-2 text-gray-400 hover:text-gray-500">
                          <span className="absolute -inset-0.5"></span>
                          <span className="sr-only">Close panel</span>
                          <div><CloseOutlined /></div>
                        </button>
                      </div>
                    </div>

                    <div className="mt-8">
                      <div className="flow-root">
                        <ul role="list" className="-my-6 divide-y divide-gray-200">
                          {
                            cartItems.map((obj:any) => {
                              return(
                                <li className="flex py-6" key={obj.id}>
                                  <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                    <img src={obj.images[0].url} className="h-full w-full object-cover object-center" />
                                  </div>

                                  <div className="ml-4 flex flex-1 flex-col">
                                    <div>
                                      <div className="flex justify-between text-base font-medium text-gray-900">
                                        <h3>
                                          <a href="#">{obj.name}</a>
                                        </h3>
                                        <p className="ml-4">{formatNumberCommas(obj.price)} &#8363;</p>
                                      </div>
                                    </div>
                                    <div className="flex flex-1 items-end justify-between text-sm">
                                      <p className="text-gray-500">
                                        <Space>
                                          <Button icon={<MinusOutlined/>} onClick={() => decreaseItemQuantity(obj)}></Button>
                                          <span>{obj.quantity}</span>
                                          <Button icon={<PlusOutlined/>} onClick={() => increaseItemQuantity(obj)}></Button>
                                        </Space>
                                      </p>

                                      <div className="flex">
                                        <button type="button" className="font-medium text-indigo-600 hover:text-indigo-500">Remove</button>
                                      </div>
                                    </div>
                                  </div>
                                </li>
                              )
                            })
                          }
                        </ul>
                      </div>
                    </div>
                  </div>

                  <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                    <div className="flex justify-between text-base font-medium text-gray-900">
                      <p>Total</p>
                      <p></p>
                    </div>
                    <div className="mt-6">
                      <a href="#" className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700">Checkout</a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    ):
    null
  )
}
