import { Link } from 'react-router-dom';
import { useState } from 'react';
import { Badge } from 'antd';
import { ShoppingOutlined, UserOutlined } from '@ant-design/icons';
import { useAuth0 } from "@auth0/auth0-react";
import { useShoppingCart } from "../context/ShoppingCart";

export function Navbar() {
  const { loginWithRedirect } = useAuth0();
  const [isOpen, openNav] = useState(false);
  let closeNavbar = () => openNav(false);
  let showNavBar = () => openNav(true);
  const {openCart, cartQuantity} = useShoppingCart();

  return (
    <div className="w-full">
      <nav className="navbar">
        <div className="menu-mobile-button md:hidden" onClick={showNavBar}>
          <i className="fa-solid fa-bars text-[25px]"></i>
        </div>
        <Link to="/">
          <img className="w-[120px]" src={process.env.PUBLIC_URL + '/assets/images/logo.png'} alt="logo"/>
        </Link>

        <div className="hidden md:flex md:space-x-10">
          <div className="nav-item" onClick={closeNavbar}><Link to="/">Home</Link></div>
          <div className="nav-item" onClick={closeNavbar}><Link to="/store">Store</Link></div>
          <div className="nav-item" onClick={closeNavbar}><Link to="/blog">Blog</Link></div>
          <div className="nav-item" onClick={closeNavbar}><Link to="/chart">Chart</Link></div>
        </div>

        <div className="flex items-center justify-between w-[20%] md:pr-[20px] md:w-[8%]">
          <a className="w-[30px]" onClick={() => loginWithRedirect()}>
            <div className="w-full"><UserOutlined /></div>
          </a>

          <span onClick={openCart}>
            <Badge count={cartQuantity}>
              <ShoppingOutlined className="text-[24px]"/>
            </Badge>
          </span>
      </div>
      </nav>

      {isOpen && (
        <div className="nav-mobile">
          <div className="absolute right-0 w-fit h-fit p-4 text-[#CCC]" onClick={closeNavbar}>
            <i className="fa-solid fa-xmark text-[30px]"></i>
          </div>
          <div className="nav-list-mobile">
            <div onClick={closeNavbar}><Link to="/">Home</Link></div>
            <div onClick={closeNavbar}><Link to="/store">Store</Link></div>
            <div onClick={closeNavbar}><Link to="/blog">Blog</Link></div>
            <div onClick={closeNavbar}><Link to="/chart">Chart</Link></div>
          </div>
        </div>
      )}
    </div>
  )
}
